body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.welcome {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: green;
}

.welcome button {
    background-color: black;
}

.fancy-button {
    position: relative;
    display: block;
    cursor: pointer;
    -webkit-perspective: 1800px;
            perspective: 1800px;
}
.fancy-button .fancy-flipper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateX(-90deg);
            transform: rotateX(-90deg);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.fancy-button .fancy-front, .fancy-button .fancy-back {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.fancy-button .fancy-front svg, .fancy-button .fancy-back svg {
    display: block;
}
.fancy-button:hover .fancy-flipper {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
}
.fancy-button:active .fancy-flipper {
    -webkit-transform: rotateX(0deg) scale(0.95);
            transform: rotateX(0deg) scale(0.95);
    -webkit-transition: -webkit-transform 0.05s ease;
    transition: -webkit-transform 0.05s ease;
    transition: transform 0.05s ease;
    transition: transform 0.05s ease, -webkit-transform 0.05s ease;
}
.fancy-button .button-text {
    letter-spacing: 0.01em;
    font-family: Futura, "Helvetica Neue", Helvetica, sans-serif;
}


.playerList {
    color: white;
}

ul.circleContainer {
    position: relative;
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 1000px;
    height: 1000px;
}

.circleItem {
    position: absolute; 
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
}


.option {
    color: #404040;

}

.option:hover:not([disabled]) {
    font-weight: 700;
    color: black !important;
    letter-spacing: 3px;
    background: none;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    cursor: pointer;
}

.option:not([disabled]) {
    border: none;
    background: #404040;
    color: #ffffff !important;
    font-weight: 100;
    padding: 20px;
    text-transform: uppercase;
    border-radius: 6px;
    display: inline-block;
}

.option[disabled] {
    padding: 20px;
    font-weight: 700;
    letter-spacing: 3px;
    background: none;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}

.optionEliminated {
    text-decoration: line-through;

}

.optionMyChoice {
    color: green !important;
}

.timer {
    position: absolute;
    margin-left: 4em;
    margin-top: 8em;
}


.answer {
    font: 600 1.5em/1 'Raleway', sans-serif;
    color: rgba(0,0,0,.5);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: .2em;
    width: 100%;
    padding-bottom: 1em;
    padding-top: 1em;
    -webkit-transition: all 1s ease 0s;
    transition: all 1s ease 0s;
}

.answerMessage {

}


.questionText {
    font: 600 1.5em/1 'Raleway', sans-serif;
    color: rgba(0,0,0,.5);
    text-align: center;
    text-transform: uppercase;
    letter-spacing: .2em;
    width: 100%;
    padding-bottom: 1em;
    padding-top: 1em;
}


.playerList {
    height: 100%; /* Full-height: remove this if you want "auto" height */
    width: 300px; /* Set the width of the sidebar */
    position: fixed; /* Fixed Sidebar (stay in place on scroll) */
    background-color: #111; /* Black */
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 20px;
}

.gameArea {
    margin-left: 300px; /* Same as the width of the sidebar */
    padding: 0px 10px;
    height: 100%
}

.screen {
    width: 100%;
    height: 100vh;
}

